.menu {
  background-color: $colorRed;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPading 0;
  }

  &__top {
    @include top-actions;
    margin-bottom: 40px;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    color: #fff;
    text-decoration: none;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }

  &__item {
    margin-bottom: 14px;
  }

  &__call {
    display: block;
    width: max-content;

    text-decoration: none;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #fff;

    &--phone {
      margin-bottom: 8px;

      &:last-child {
        margin-bottom: 16px;
      }
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid #fff;
    }
  }
}
